
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import axios from 'axios'
export default {
name: 'PersonalCenter',
components: {
Tab,
TabItem,
Tabbar,
TabbarItem,
Group,
Cell,
XHeader,
XInput,
XButton,
Actionsheet,
CellBox
},
data () {
return {

userName: '',
name: '',
sheetVisible: false,
data: {
menu1: '拍照',
menu2: '从相册中选择'
}
}
},
mounted () {
this.showData()
},
methods: {
actionsheet () {
this.sheetVisible = true
},
getCamera () {
console.log('拍照')
},
getImage () {
console.log('相册')
},
showData () {
this.userName = localStorage.getItem('username')
axios.get('http://47.113.124.225:8083/Self', {
params: {
stno: this.userName
}
}).then((response) => {
this.name = response.data.data.name
}, (response) => {

})
},
LoginOut () {
localStorage.removeItem('username')
localStorage.removeItem('Flag')
localStorage.removeItem('isStudent')
localStorage.clear()
this.$router.push('/')
},
senTog (ele) {
if(ele == 1){
this.$router.push('/Home')
}else{
this.$router.push('/PersonalCenter')
}

}
}
}



