
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import axios from 'axios'
import qs from 'qs'
export default {
name: 'SignIn',
data () {
return {
courseid: '',
show1: false,
show2: false,
show3: false,
show4: false,
userName: '',
time: '',
resdata: '',
weekDay:
['星期天', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],
list: [],
postData: [],
userId: '',
lat:'',
lng:'',
dizhi:''

}
},
directives: {
TransferDom
},
mounted () {
this.userId = localStorage.getItem('userId')
this.showData()
this.getdz()
},
components: {
Group,
Cell,
XHeader,
XInput,
XButton,
Alert,
Toast
},
methods: {
getdz () {
var data = {
key: "PQABZ-CUKEX-CRE4P-7DEQJ-QHVVH-X7FRX"  
};
var url = "https://apis.map.qq.com/ws/location/v1/ip"; 
data.output = "jsonp";
this.$jsonp(url, data)
.then(res => {
this.lat = res.result.location.lat;
this.lng = res.result.location.lng;
var datas = {
key: "PQABZ-CUKEX-CRE4P-7DEQJ-QHVVH-X7FRX",  //这个key就是你申请的密钥
location:this.lat+","+this.lng
};
var urls = "http://apis.map.qq.com/ws/geocoder/v1/"; 
datas.output = "jsonp";

this.$jsonp(urls, datas)
.then(res => {
this.dizhi = res.result.address
})
.catch(error => {
console.log(error);
});

})
.catch(error => {
console.log(error);
});

},
togUrl () {
console.log(2333333333333);
},
utc2beijing: function (time) {
var dateee = new Date(time).toJSON()
this.date = new Date(+new Date(dateee) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
return this.date
},
showData: function () {
this.userName = localStorage.getItem('username')
this.courseid = localStorage.getItem('courseid')
axios.get('http://47.113.124.225:8083/Student/SignRecord', {
params: {
courseid: this.courseid,
studentid: this.userName
}
}).then((res) => {
for (var i = 0; i < res.data.data.length; i++) {
res.data.data[i].signTime = this.$options.methods.utc2beijing(res.data.data[i].signTime)
}
axios.post('http://www.xhw8888.com/index/app/qddw', qs.stringify({
data: res.data.data,
}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
this.list = res.data
})

if (res.data.code === 200) {
} else if (res.data.code === 10001) {
}
}, (response) => {

})
},
handleSubmit: function () {
this.userName = localStorage.getItem('username')
this.courseid = localStorage.getItem('courseid')
axios.put('http://47.113.124.225:8083/Student/SignRecord', qs.stringify({
courseid: this.courseid,
studentid: this.userName
}), {
headers: {
token: 'true'
}
}).then(res => {
this.resdata = res.data.data
if (res.data.code === 200) {

axios.post('http://www.xhw8888.com/index/app/adddw', qs.stringify({
courseid: this.courseid,
userId: this.userId,
lng: this.lng,
lat: this.lat
}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
this.show2 = true
this.showData();
})

} else if (res.data.msg === '签到超时') {
this.show1 = true 
} else if (res.data.msg === '请勿重复签到') {
this.show3 = true 
} else if (res.data.msg === '不存在此签到') {
this.show4 = true 
}
})
}
}
}



