
import XHeader from 'vux/src/components/x-header/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'
import XInput from '../../node_modules/vux/src/components/x-input/index.vue'
import Group from '../../node_modules/vux/src/components/group/index.vue'
import axios from 'axios'
import qs from 'qs'
export default {
name: 'ForgetPassword',
directives: {
TransferDom
},
data () {
return {
ClassNumber: '',
PhoneNumber: '',
VerCode: '',
PassWord: '',
demo1: false,
value: false,
btnBoolen: false,
btnClassName: 'btn',
btnValue: '获取短信验证码',
show1: false,
ShowText: '',
disabled: true
}
},
components: {
Group,
XInput,
ViewBox,
XHeader,
CheckIcon,
XButton,
XSwitch,
Alert,
Toast
},
methods: {
getVerifyCode () {

if (this.validatePhone()) {

axios.post('http://47.113.124.225:8083/Code2', qs.stringify({
tel: this.PhoneNumber
}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
console.log(res.data)
if (res.data.code === 10004) {
this.show1 = true
this.ShowText = '不存在该用户'
} else {
this.validateBtn()
}
})
.catch(function (error) {
console.log(error)
})
}
},
validatePhone () {

if (!this.PhoneNumber) {
this.show1 = true
this.ShowText = '手机号码不能为空'
console.log(this.errors)
return false
} else if (!/^1[345678]\d{9}$/.test(this.PhoneNumber)) {
this.show1 = true
this.ShowText = '手机号码非法'
console.log(this.errors)
return false
} else {
this.errors = {}
this.btnBoolen = false
this.btnClassName = 'btns'
return true
}
},
validateBtn () {

let time = 60
let timer = setInterval(() => {
if (time === 0) {
clearInterval(timer)
this.btnBoolen = false
this.btnValue = '获取验证码'
} else {
this.btnValue = time + '秒后重试'
this.btnBoolen = true
time--
}
}, 1000)
},
handleSubmit: function () {
if (this.PhoneNumber.length !== 11) {
this.show1 = true
this.ShowText = '请输入有效的手机号码，需是11位'
} else if (this.VerCode.length !== 6) {
this.show1 = true
this.ShowText = '请输入6位验证码'
} else if (this.PassWord.length <= 6) {
this.show1 = true
this.ShowText = '密码必须大于6位'
} else {
axios.post('http://47.113.124.225:8083/forgetpassword', qs.stringify({
tel: this.PhoneNumber,
code: this.VerCode,
password: this.PassWord
}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
console.log(res.data)
if (res.data.code === 10001) {
if (res.data.msg === '验证码错误') {
this.show1 = true
this.ShowText = '验证码错误'
} else if (res.data.msg === '操作超时') {
this.show1 = true
this.ShowText = '验证码失效'
}
} else {
this.show1 = true
this.ShowText = '修改密码成功'
this.$router.push('/')
}
}).catch(function (error) {
console.log(error)


})
}
}
},
updated () {
if (this.PhoneNumber !== '' && this.VerCode !== '' && this.PassWord !== '') {
this.disabled = false
}
}
}




