
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


import axios from 'axios'

import qs from 'qs'
export default {
name: 'SignIn',
data () {
return {
serche_key : '',
data_list:[],
isShow:false
}
},
directives: {
TransferDom
},
mounted () {
var id = this.$route.query.id
this.userId = localStorage.getItem('userId')
this.username = localStorage.getItem('username')
this.courseid = localStorage.getItem('courseid')
if (id != undefined){
this.serche_key = id
this.serach()
}
},
components: {
Group,
Cell,
XHeader,
XInput,
XButton,
Alert,
Toast,
Grid,
GridItem,
Tab,
TabItem,
Tabbar,
TabbarItem,
},
methods: {
serach () {
axios.get('http://www.xhw8888.com/index/app/serach?key=' + this.serche_key + "&userid=" + this.userId, {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
console.log(res)
if(res.data != 'no'){
this.isShow = true
this.data_list = res.data
}else{
this.isShow = false
}

})
},
sendBk (id,show) {
if(show == 1){
return false;
}
this.$dialog.confirm({
title: '提示',
message: '确认加入吗？',
})
.then(() => {

axios.get('http://www.xhw8888.com/index/app/addbk?coursid=' + id + "&userid=" + this.userId, {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
if(res.data == 1){
this.$dialog.alert({
title: '',
message: '加入成功',
})
this.serach();
}






})
})
.catch(() => {

console.log(222222222)
});
}
}
}



