
import XHeader from 'vux/src/components/x-header/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'
import XInput from '../../node_modules/vux/src/components/x-input/index.vue'
import Group from '../../node_modules/vux/src/components/group/index.vue'
import axios from 'axios'
import qs from 'qs'
export default {
name: 'PerfectInfo',
data () {
return {
names: '',
major: '',
academy: '',
classid: '',
param: 1
}
},
components: {
Group,
XInput,
ViewBox,
XHeader,
CheckIcon,
XButton,
XSwitch,
Alert,
AlertModule,
Checker,
CheckerItem
},
methods: {
check: function () {
if (this.names === '') {
console.log('名字不能为空')
} else if (this.major === '') {
console.log('专业不能为空')
} else if (this.academy === '') {
console.log('学院不能为空')
} else {
const dataB = JSON.parse(localStorage.getItem('info'))
axios.put('http://47.113.124.225:8083/user', qs.stringify({
stno: dataB.stno,
name: this.names,
tel: dataB.tel,
academy: this.academy,
major: this.major,
roleid: this.param,
classid: this.classid
}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
if (res.data.code === 200) {
console.log(res.data.code)
console.log('注册成功')
this.$router.push('/')
}
}).catch(function (error) {
console.log(error)
})
}
},
change (val, label) {
console.log('change', val, label)
},
getRadioVal (event) {
let radioVal = event.target.value
console.log(radioVal)
}
}
}




