
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios'
import qs from 'qs'
export default {
name: 'PersonalInfo',
components: {
Group,
Cell,
XHeader,
XInput,
XButton
},
data () {
return {
userName: '',
name: '',
majors: '',
academy: '',
tel: '',
stno: '',
xuexiao:'',
sex:'',
list: []
}
},
mounted () {
this.showData()
},

methods: {
showData: function () {
this.userName = localStorage.getItem('username')
console.log(this.userName)
axios.get('http://www.xhw8888.com/index/app/SelfUserInfo?stno='+this.userName).then((response) => {

this.name = response.data.Name
this.majors = response.data.Major
this.academy = response.data.Academy
this.stno = response.data.STNo
this.tel = response.data.Tel
this.sex = response.data.sex
this.xuexiao = response.data.xuexiao
}, (response) => {

})
},
check () {
var sex;
var uid = localStorage.getItem('username')
axios.post('http://www.xhw8888.com/index/app/Bcuserinfo', qs.stringify({
name: this.name,
majors: this.majors,
academy: this.academy,
stno: this.stno,
tel: this.tel,
xuexiao : this.xuexiao,
uid : uid
}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
this.$dialog.alert({
title: '',
message: '编辑成功',
})
this.showData()
})
}
}
}



