
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


import axios from 'axios'

import qs from 'qs'
export default {
name: 'SignIn',
data () {
return {
value1: '2015-11-12',
coursname: '',
class_b: '',
userid: '',
username: '',
viewShow:true,
code: '',
courseid:'',
coursdata:[],
userlistshow:false,
tabShow : false,
userData: []
}
},
directives: {
TransferDom
},
mounted () {
this.userId = localStorage.getItem('userId')
this.username = localStorage.getItem('username')
this.courseid = localStorage.getItem('courseid')
console.log(this.courseid)
this.getData()
this.getmember()
},
components: {
Group,
Cell,
XHeader,
XInput,
XButton,
Alert,
Toast,
Grid,
GridItem,
Tab,
TabItem,
Tabbar,
TabbarItem,
},
methods: {
getData () {
axios.get('http://www.xhw8888.com/index/app/bkxq?id=' + this.courseid, {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
console.log(res)
this.coursdata = res.data[0]






})
},
getmember () {
axios.get('http://www.xhw8888.com/index/app/member?courseid=' + this.courseid, {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
if(res.data != 'no'){
this.userlistshow = true
this.userData = res.data
}else{
this.userData = []
this.userlistshow = false
}

})
},
senTog (ele) {
console.log(111111111);
if (ele == 1){
this.tabShow = false
}else if(ele == 2){
this.tabShow = true
}
},
logoutbk () {
axios.get('http://www.xhw8888.com/index/app/deletebk?courseid=' + this.courseid, {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
this.$dialog.alert({
title: '',
message: '结束成功',
})
this.$router.push('/Home')
})
}
}
}



