
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import QRCode from 'qrcode'
export default{
data(){
return{
codeUrl: '',
isShow:true,
id:'',
courseid:''
}
},
components:{
Group,
Cell,
XHeader,
XInput,
XButton,
Alert,
Toast,
Datetime,
QRCode
},
mounted () {
this.courseid = localStorage.getItem('courseid')
setTimeout(()=>{
this.getUrl('http://47.113.124.225/:8080/#/addbk?id='+this.courseid)
},2000)
},
created(){
},
methods: {
getUrl(xxx){

var canvas=document.getElementById("canvas"); 

var code=document.getElementById("code");   

QRCode.toCanvas(canvas,xxx, error=> {

if (error) console.error(error);

console.log("success!");

});

var image = new Image();     

image.src = canvas.toDataURL("image/png");  

image.style.width = "50%";    

code.appendChild(image);     

canvas.style.display = "none";   

},
}
};



