
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'

import axios from 'axios'
import qs from 'qs'
export default {
name: 'Login',
directives: {
TransferDom
},
data () {
return {
userName: '',
passWord: '',
show1: false,
show2: false,
show3: false,
show4: false,
resData: [],
postData: []
}
},
components: {
Group,
Cell,
XHeader,
XInput,
XButton,
Alert
},
methods: {
handleSubmit: function () {
if (this.userName === '') {
this.show1 = true
} else if (this.passWord === '') {
this.show2 = true
} else {
axios.post('http://47.113.124.225:8083/login', qs.stringify({
stno: this.userName,
password: this.passWord
}), {
headers: {
token: 'true'
}
}).then(res => {
if (res.data.code === 200) {
localStorage.setItem('username', this.userName)
localStorage.setItem('userId', res.data.data.userId)
axios.get('http://47.113.124.225:8083/Self?stno=' + this.userName, {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
if (res.data.code === 200) {
console.log(res);
localStorage.setItem('roleId', res.data.data.roleId)
} else {
localStorage.setItem('roleId', 1)
}
})




localStorage.setItem('Flag', 'isLogin')
if (res.data.data.flag === 1) {
this.$router.push('/TeacherHome')
localStorage.setItem('isStudent', '0')
} else {
this.$router.push('/Home')
localStorage.setItem('isStudent', '1')
}
} else if (res.data.code === 10001) {
this.passWord = ''
this.show4 = true
} else if (res.data.code === 10004) {
this.userName = ''
this.show3 = true
}
})
}
}
},
created  () {













let getFlag = localStorage.getItem('Flag')
let isStudent = localStorage.getItem('isStudent')
if (getFlag === 'isLogin') {
if (isStudent === '1') {
this.$router.push('/Home')
} else {
this.$router.push('/TeacherHome')
}
} else {
this.$router.push('/')
}
}
}



