
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


let scan = null;

document.addEventListener("plusready", function() {

plus.key.addEventListener('backbutton', function() {

scan.close();
window.history.back();
}, false);
});
export default{
data(){
return{
codeUrl: '',
isShow:true,
id:''
}
},
components:{
Group,
Cell,
XHeader,
XInput,
XButton,
Alert,
Toast,
Datetime,
},
mounted () {
this.startScan()
},
created(){
this.startRecognize();
this.startScan();
},
methods: {

getUrl(xxx){

var canvas=document.getElementById("canvas"); 

var code=document.getElementById("code");   

QRCode.toCanvas(canvas,xxx, error=> {

if (error) console.error(error);

console.log("success!");

});

var image = new Image();     

image.src = canvas.toDataURL("image/png");  

image.style.width = "100%";    

code.appendChild(image);     

canvas.style.display = "none";   

},
startRecognize() {
let that = this;
if (!window.plus) return;
that.isShow=false;

scan = new plus.barcode.Barcode('bcid');

scan.onmarked = onmarked;
function onmarked(type, result, file) {
switch (type) {
case plus.barcode.QR:
type = 'QR';
break;
case plus.barcode.EAN13:
type = 'EAN13';
break;
case plus.barcode.EAN8:
type = 'EAN8';
break;
default:
type = '其它' + type;
break;
}
result = result.replace(/\n/g, '');
that.codeUrl = result;
var id = result.split('=')
that.id = id[1]
scan.cancel();
scan.close();
if(that.codeUrl){
that.isShow=true
}
that.togUrl(id[1])
}
},
togUrl(id){
this.$router.push('/addbk?id='+id)
},

startScan() {
if (!window.plus) return;
this.startRecognize()
scan.start();
},

cancelScan(){
console.log(11111111);
this.isShow=true;
scan.cancel();
scan.close();
}
}
};



