
import Popover from 'vux/src/components/popover/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios'
import qs from 'qs'
export default {
name: 'home',
data () {
return {
list: [],
userName: '',
imgPath: '',
roleId:2,
userId:'',
listShow:false,
active:'b'
}
},
components: {
Popover,
Flexbox,
FlexboxItem,
Tab,
TabItem,
Tabbar,
TabbarItem,
Group,
Cell,
XHeader,
XInput,
XButton
},
mounted () {
this.roleId = localStorage.getItem('roleId')
this.userId = localStorage.getItem('userId')





this.active = 'b'

this.showData()
this.roleId = localStorage.getItem('roleId')

},
methods: {
toCourse (id) {
localStorage.setItem('courseid', id)
this.$router.push('/bkxq')
},
toCoursemy (id) {
localStorage.setItem('courseid', id)
this.$router.push('/xxbkxq')
},
toPersonalCenter () {
this.$router.push('/PersonalCenter')
},
showData () {
this.userName = localStorage.getItem('username')
axios.post('http://www.xhw8888.com/index/app/Course', qs.stringify({
userid: this.userId,
active: this.active
}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
if(res.data == 'no'){
this.list = []
this.listShow = true
}else{
this.listShow = false
this.list = res.data
}

})
.catch(function (error) {
console.log(error)
})
},
onShow () {
console.log('on show')
},
onHide () {
console.log('on hide')
},
togurl (ele) {
if (ele == 1) {
this.$router.push('/addbk')
}
if (ele == 2) {
this.$router.push('/Createcourse')
}
if (ele == 3) {
this.$router.push('/sys')
}
},
senTog (ele) {

if (ele == 1) {
this.$router.push('/Home')
} else {
this.$router.push('/PersonalCenter')
}
},
onChange (ele) {
this.active = ele
this.showData()
}
}
}



