
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import axios from 'axios'
import qs from 'qs'
export default {
name: 'PublishSign',
data () {
return {
courseId: 1,
timestamp: null,
show1: false,
show2: false,
show3: false,
message: 'Hello',
userName: '',
time: '',
signid: '',
resdata: '',
dizhi:'',
weekDay:
['星期天', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],
list: [
],
postData: []
}
},
directives: {
TransferDom
},
mounted () {
this.userName = localStorage.getItem('username')
this.courseId = localStorage.getItem('courseid')
this.showData()
this.utc2beijing()
this.getData();
},
components: {
Confirm,
Group,
Cell,
XHeader,
XInput,
XButton,
Alert,
Toast
},
methods: {
getData () {
axios.get('http://www.xhw8888.com/index/app/bkxq?id=' + this.courseId, {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
console.log(res.data);
this.dizhi = res.data[0].dizhi
})
},
utc2beijing: function (time) {
var dateee = new Date(time).toJSON()
var date = new Date(+new Date(dateee) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
return date
},
showData: function () { // 直接显示教师本课程的历史发布签到

axios.get('http://47.113.124.225:8083/Teacher/SignIn', {
params: {
courseid: this.courseId
}
}).then((res) => {
for (var i = 0; i < res.data.data.length; i++) {
res.data.data[i].signInTime = this.$options.methods.utc2beijing(res.data.data[i].signInTime)
}
this.list = res.data.data
console.log(this.list)
if (res.data.code === 200) {

} else if (res.data.code === 10001) {
}
}, (response) => {

})
},
getdz () {
var data = {
key: "PQABZ-CUKEX-CRE4P-7DEQJ-QHVVH-X7FRX"  
};
var url = "https://apis.map.qq.com/ws/location/v1/ip"; 
data.output = "jsonp";
this.$jsonp(url, data)
.then(res => {
var lat = res.result.location.lat;
var lng = res.result.location.lng;
var datas = {
key: "PQABZ-CUKEX-CRE4P-7DEQJ-QHVVH-X7FRX",  //这个key就是你申请的密钥
location:lat+","+lng
};
var urls = "http://apis.map.qq.com/ws/geocoder/v1/"; 
datas.output = "jsonp";

this.$jsonp(urls, datas)
.then(res => {
this.dizhi = res.result.address
})
.catch(error => {
console.log(error);
});

})
.catch(error => {
console.log(error);
});

},
showConfirm: function () { // 显示输入持续时间的弹窗
this.show2 = true
},
handleSubmit: function (value) { // 发布签到
console.log(parseFloat(value));
axios.post('http://www.xhw8888.com/index/app/sfqd', qs.stringify({
duration: parseFloat(value),
}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
if(res.data == 401) {
this.$dialog.alert({
title: '',
message: '签到时长超出限制',
})
return false;
}else{
this.userName = localStorage.getItem('username')
this.courseId = localStorage.getItem('courseid')
axios.post('http://47.113.124.225:8083/Teacher/SignIn', qs.stringify({
courseid: this.courseId,
studentid: this.userName,
duration: parseFloat(value)
}), {
headers: {
token: 'true'
}
}).then(res => {
this.resdata = res.data.data
console.log(res.data.code)
if (res.data.code === 200) {
this.show3 = true


this.showData()
this.utc2beijing()
this.getData();
} else if (res.data.code === 10001) {
this.show1 = true
}
})
}
})


},
handleInfo: function (id) { // 点击查看某次签到的详情
console.log('当前被点击的id=' + id)
this.signid = parseInt(id)
localStorage.setItem('signid', this.signid)
axios.get('http://47.113.124.225:8083/Teacher/SignRecord', {
params: {
signid: parseInt(id)
}
}).then(res => {
console.log(res.data.data)
if (res.data.code === 200) {
this.$router.push('/TeacherSignrecord')
} else if (res.data.code === 10001) {
this.show1 = true
}
})
}
}
}



