
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'

import axios from 'axios'

export default {
name: 'TeacherSignrecord',
data () {
return {
courseId: 1,
show1: false,
userName: '',
time: '',
signid: '',
resdata: '',
weekDay:
['星期天', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],
list: [],
postData: []
}
},
directives: {
TransferDom
},
mounted () {
this.showData()
},
components: {
Group,
Cell,
XHeader,
XInput,
XButton,
Alert
},
methods: {
utc2beijing: function (time) {
var dateee = new Date(time).toJSON()
var date = new Date(+new Date(dateee) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
return date
},
showData: function () {
this.userName = localStorage.getItem('username')
this.signid = localStorage.getItem('signid')
console.log(this.signid)
axios.get('http://47.113.124.225:8083/Teacher/SignRecord', {
params: {
signid: this.signid
}
}).then((res) => {
for (var i = 0; i < res.data.data.length; i++) {
res.data.data[i].signTime = this.$options.methods.utc2beijing(res.data.data[i].signTime)
}
this.list = res.data.data
console.log(this.list)
if (res.data.code === 200) {
} else if (res.data.code === 10001) {
}
}, (response) => {

})
}
}
}



