
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


import axios from 'axios'

import qs from 'qs'
import QRCode from 'qrcode'
export default {
name: 'SignIn',
data () {
return {
value1: '',
coursname: '',
class_b: '',
userid: '',
username: '',
viewShow:true,
code: '',
lat:'',
lng:'',
dizhi:'',
date: '',
value2:'',

show: false,
}
},
directives: {
TransferDom
},
mounted () {
this.userId = localStorage.getItem('userId')
this.username = localStorage.getItem('username')
this.getDZ()
},
components: {
Group,
Cell,
XHeader,
XInput,
XButton,
Alert,
Toast,
Datetime,
QRCode
},
methods: {
getUrl(xxx){

var canvas=document.getElementById("canvas"); 

var code=document.getElementById("code");   

QRCode.toCanvas(canvas,xxx, error=> {

if (error) console.error(error);

console.log("success!");

});

var image = new Image();     

image.src = canvas.toDataURL("image/png");  

image.style.width = "50%";    

code.appendChild(image);     

canvas.style.display = "none";   

},
formatDate(date) {
return `${date.getMonth() + 1}/${date.getDate()}`;
},
onConfirm(date) {
const [start, end] = date;
this.show = false;
this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`;
},
getDZ () {
var data = {
key: "PQABZ-CUKEX-CRE4P-7DEQJ-QHVVH-X7FRX"  
};
var url = "https://apis.map.qq.com/ws/location/v1/ip"; 
data.output = "jsonp";
this.$jsonp(url, data)
.then(res => {
this.lat = res.result.location.lat;
this.lng = res.result.location.lng;
var datas = {
key: "PQABZ-CUKEX-CRE4P-7DEQJ-QHVVH-X7FRX",  //这个key就是你申请的密钥
location: this.lat+","+this.lng
};
var urls = "http://apis.map.qq.com/ws/geocoder/v1/"; 
datas.output = "jsonp";

this.$jsonp(urls, datas)
.then(res => {
this.dizhi = res.result.address
})
.catch(error => {
console.log(error);
});

})
.catch(error => {
console.log(error);
});
},
change (value) {
console.log('change', value)
},
log (str1, str2 = '') {
console.log(str1, str2)
},
onConfirm (val) {
this.value1 = val
console.log('on-confirm arg', val)
console.log('current value', this.value1)
},
onConfirm2 (val) {
this.value2 = val
console.log('on-confirm arg', val)
console.log('current value', this.value1)
},
addcourse () {

if (this.coursname == '') {
this.$Message.warning('课程名称不能为空')
return false
}

if (this.class_b == '') {
this.$Message.warning('班级不能为空')
return false
}
console.log(this.value2)
console.log(this.value1)
if (this.value2 == '' || this.value1 == '') {
this.$Message.warning('开始日期/结束日期不能为空')
return false
}
var riqi = this.value1 + "/" + this.value2
axios.post('http://www.xhw8888.com/index/app/addcourse', qs.stringify({
coursname: this.coursname,
class_b: this.class_b,
user_id: this.userId,
ExamTime: riqi,
username: this.username,
lat : this.lng,
lng : this.lng,
dizhi : this.dizhi

}), {
emulateJSON: true
}, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
}
}).then(res => {
this.code = res;
this.viewShow = false
setTimeout(()=>{
this.getUrl('http://47.113.124.225:8080/#/addbk?id='+res.data)
},2000)
})
},
startClass () {
localStorage.setItem('courseid', this.code.data)
this.$router.push('/bkxq')
}
}
}



